@use 'base/variables' as v;
.alerta{
    padding: calc(v.$separacion / 5);
    color: v.$blanco;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;

    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
}